import vtkPolyData from "@kitware/vtk.js/Common/DataModel/PolyData";

class PolyDataExtractor {
    constructor(polyData) {
        console.assert(polyData.getLinks(), 'PolyDataExtractor: No links built for polydata');
        this.polyData = polyData;
        this.visitedCells = new Array(polyData.getNumberOfCells()).fill(false);
        this.visitedPoints = new Array(polyData.getNumberOfPoints()).fill(false);
    }

    _extractGroup = (neighborIds) => {
        const cellGroup = [];
        const pointGroup = [];
        this.visitedCells[neighborIds[0]] = true;

        // Iterate over cells (breadth-first search)
        while (neighborIds.length > 0) {
            const currentCellId = neighborIds.shift();
            cellGroup.push(currentCellId);

            // Iterate over cell points
            const pointIds = this.polyData.getCellPoints(currentCellId).cellPointIds;
            for (const pointId of pointIds) {
                if (!this.visitedPoints[pointId]) {
                    this.visitedPoints[pointId] = true;
                    pointGroup.push(pointId);

                    // Add unvisited cells to the queue
                    for (const neighborCellId of this.polyData.getPointCells(pointId)) {
                        if (!this.visitedCells[neighborCellId]) {
                            this.visitedCells[neighborCellId] = true;
                            neighborIds.push(neighborCellId);
                        }
                    }
                }
            }
        }

        return [cellGroup, pointGroup]
    }

    _buildExtractedPolyData = (cellGroup, pointGroup) => {
        const pBuffer = []; // Points
        const tBuffer = []; // Cells
        const pointIdMap = {};
        // Map former point IDs to new point IDs
        pointGroup.forEach((pointId, idx) => {
            pointIdMap[pointId] = idx;
            const points = this.polyData.getPoints().getPoint(pointId);
            pBuffer.push(...points);
        });
        for (const cellId of cellGroup) {
            const pointIds = this.polyData.getCellPoints(cellId).cellPointIds;
            const newPointIds = pointIds.map(pointId => pointIdMap[pointId]);
            tBuffer.push(3);
            tBuffer.push(...newPointIds);
        }

        const extractedPolyData = vtkPolyData.newInstance();
        extractedPolyData.getPoints().setData(new Float32Array(pBuffer), 3);
        extractedPolyData.getPolys().setData(new Uint32Array(tBuffer));
        extractedPolyData.getPolys().setNumberOfComponents(4);

        return extractedPolyData;
    }

    extractGroups = () => {
        const cellGroups = [];
        const pointGroups = [];
        const extractedPolyDatas = [];
        for (let cellId = 0; cellId < this.polyData.getNumberOfCells(); cellId++) {
            if (!this.visitedCells[cellId]) {
                const [cellGroup, pointGroup] = this._extractGroup([cellId]);
                const extractedPolyData = this._buildExtractedPolyData(cellGroup, pointGroup);
                extractedPolyDatas.push(extractedPolyData);
            }
        }

        return extractedPolyDatas;
    }

}

export default PolyDataExtractor;
