export function antiAlias(imageData, width, height, intensity) {
    const resultImageData = imageData.slice();

    for (let y = 1; y < height - 1; y++) {
        for (let x = 1; x < width - 1; x++) {
            const index = y * width + x;
            const gray =
                (imageData[(y - 1) * width + x - 1] * intensity +
                    imageData[(y - 1) * width + x] * intensity +
                    imageData[(y - 1) * width + x + 1] * intensity +
                    imageData[y * width + x - 1] * intensity +
                    imageData[y * width + x] +
                    imageData[y * width + x + 1] * intensity +
                    imageData[(y + 1) * width + x - 1] * intensity +
                    imageData[(y + 1) * width + x] * intensity +
                    imageData[(y + 1) * width + x + 1] * intensity) /
                1 + intensity * 8;

            // Mettre  jour la valeur de pixel dans l'image rsultante
            resultImageData[index] = gray;
        }
    }

    return resultImageData;
}

export function sharpen(imageData, width, height) {
    // Crer une copie de l'image pour ne pas modifier l'original
    const resultImageData = imageData.slice();

    // Matrice de renforcement
    const sharpenMatrix = [
        [0, -1, 0],
        [-1, 5, -1],
        [0, -1, 0],
    ];

    const matrixSize = 3; // Taille de la matrice (3x3)

    for (let y = 1; y < height - 1; y++) {
        for (let x = 1; x < width - 1; x++) {
            let sum = 0;

            // Appliquer la matrice de renforcement aux pixels voisins
            for (let i = 0; i < matrixSize; i++) {
                for (let j = 0; j < matrixSize; j++) {
                    const pixelX = x + j - 1;
                    const pixelY = y + i - 1;
                    const index = pixelY * width + pixelX;
                    sum += imageData[index] * sharpenMatrix[i][j];
                }
            }

            // S'assurer que la valeur est dans la plage [0, 255]
            sum = Math.min(32767, Math.max(-32768, sum));

            // Mettre  jour la valeur de pixel dans l'image rsultante
            const index = y * width + x;
            resultImageData[index] = sum;
        }
    }

    return resultImageData;
}

export function applySmoothing(imageData, width, height, intensity) {
    const resultImageData = new Uint8ClampedArray(imageData);

    const kernel = [
        [1, 1, 1],
        [1, 1, 1],
        [1, 1, 1],
    ]; // Noyau de convolution simple pour l'adoucissement.

    const kernelSize = kernel.length;
    const halfKernelSize = Math.floor(kernelSize / 2);

    for (let y = halfKernelSize; y < height - halfKernelSize; y++) {
        for (let x = halfKernelSize; x < width - halfKernelSize; x++) {
            let sum = 0;

            for (let ky = 0; ky < kernelSize; ky++) {
                for (let kx = 0; kx < kernelSize; kx++) {
                    const pixelX = x + kx - halfKernelSize;
                    const pixelY = y + ky - halfKernelSize;
                    const index = pixelY * width + pixelX;

                    // Appliquer le noyau de convolution au pixel.
                    sum += imageData[index] * kernel[ky][kx];
                }
            }

            // Normaliser la somme en fonction de l'intensit.
            sum = (1 - intensity) * (imageData[y * width + x] + (intensity / 9) * sum);

            // Mettre  jour la valeur du pixel dans l'image rsultante.
            resultImageData[y * width + x] = sum;
        }
    }

    return resultImageData;
}

export function calculateNormalCurve(curve, ind, nbNormals) {
    const norm = [];

    if (curve.length <= 1 || ind < 0 || ind >= curve.length) {
        // Gestion des cas spciaux o la normale ne peut pas tre calcule
        return norm;
    }

    // Coordonnes du point "p"
    const [px, py] = curve[ind];

    // Calcul des tangentes aux points voisins
    let [tx, ty] = [0, 0]; // Tangente au point "p"

    if (ind > 10) {
        const [prevX, prevY] = curve[ind - 10];
        tx += px - prevX;
        ty += py - prevY;
    }

    if (ind < curve.length - 10) {
        const [nextX, nextY] = curve[ind + 10];
        tx += nextX - px;
        ty += nextY - py;
    }

    // Calcul de la normale (perpendiculaire  la tangente)
    const magnitude = Math.sqrt(tx * tx + ty * ty);
    if (magnitude === 0) {
        // Gestion du cas o la tangente est nulle (point isol)
        return norm;
    }

    const nx = -ty / magnitude; // Composante x de la normale
    const ny = tx / magnitude; // Composante y de la normale

    // Ajout du point "p" dans "norm"
    norm.push([px, py]);

    const stepSize = magnitude / (nbNormals / 2); // nbNormals points au total, y compris "p"
    let currentX = px - nx * magnitude;
    let currentY = py - ny * magnitude;

    // Calcul des points quidistants sur la normale
    for (let i = 0; i < nbNormals; i++) {
        currentX += nx * stepSize;
        currentY += ny * stepSize;
        norm.push([currentX, currentY]);
    }

    return norm;
}

export function cubicSplineInterpolation(arr, x) {
    const n = arr.length;
    let i = 0;

    if (x >= arr[n - 1][0]) {
        i = n - 2;
    } else {
        while (x > arr[i + 1][0]) {
            i++;
        }
    }

    const x1 = arr[i][0];
    const x2 = arr[Math.min(i + 1, n - 1)][0];

    const y0 = arr[Math.max(i - 1, 0)][1];
    const y1 = arr[i][1];
    const y2 = arr[Math.min(i + 1, n - 1)][1];
    const y3 = arr[Math.min(i + 2, n - 1)][1];

    const t = (x - x1) / (x2 - x1);
    const t2 = t * t;
    const t3 = t * t2;

    const a = -0.5 * y0 + 1.5 * y1 - 1.5 * y2 + 0.5 * y3;
    const b = y0 - 2.5 * y1 + 2 * y2 - 0.5 * y3;
    const c = -0.5 * y0 + 0.5 * y2;
    const d = y1;

    const y = a * t3 + b * t2 + c * t + d;

    return [x, y];
}

function linearInterpolationCoefficients(point1, point2) {
    const [x1, y1] = point1;
    const [x2, y2] = point2;

    if (x1 === x2) {
        throw new Error('Les deux points doivent avoir des coordonnes x diffrentes pour l\'interpolation linaire.');
    }

    const a = (y2 - y1) / (x2 - x1);
    const b = y1 - a * x1;

    return [Math.round(a), Math.round(b)];
}

//Complete cubic spline interpolation in order to have linear interpolation in the beginnign and in the end of the curve
function completeInterpolation(interpolatedPoints, xMax){
    const [a0, b0] = linearInterpolationCoefficients(interpolatedPoints[0], interpolatedPoints[10])
    const [a1, b1] = linearInterpolationCoefficients(interpolatedPoints[interpolatedPoints.length - 10], interpolatedPoints[interpolatedPoints.length - 1])
    const stepBegin = interpolatedPoints[0][0] / (xMax / 3)
    const stepEnd = (xMax - interpolatedPoints[interpolatedPoints.length - 1][0]) / (xMax / 3)
    for(let x = 0; x < interpolatedPoints[0][0]; x += stepBegin){
        interpolatedPoints.push([x, a0 * x + b0])
    }
    interpolatedPoints.sort((a, b) => a[0] - b[0]);

    for(let x = interpolatedPoints[interpolatedPoints.length - 1][0] + stepEnd; x < xMax; x += stepEnd){
        interpolatedPoints.push([x, a1 * x + b1])
    }

    return  interpolatedPoints.map(p => {
        return [p[0], p[1], 0]
    })
}


