import {awrap} from "@babel/runtime/helpers/regeneratorRuntime";

const zeros = require('zeros')
// const sharp = require('sharp')

export const dcmbufferToPng = async (metadata, invertRows = false) => {
    let j;
    let i;
    var BitsAllocated = metadata.BitsAllocated
    var BitsStored = metadata.BitsStored
    var rows = metadata.Rows
    var columns = metadata.Columns
    var windowCenter = metadata.WindowCenter
    var windowWidth = metadata.WindowWidth


    let pixelArray;
    let pixelData = metadata.PixelData;
    if (BitsAllocated === 16)
        pixelArray = new Int16Array(pixelData);//new Uint16Array(dataSet.byteArray.buffer, pixelDataElement.dataOffset, pixelDataElement.length / 2);
    else
        pixelArray = new Int8Array(pixelData);//new Uint8Array(dataSet.byteArray.buffer, pixelDataElement.dataOffset, pixelDataElement.length);
    var res = zeros([rows, columns])
    if(invertRows) {
        for (i = 1; i <= rows; i++) {
            for (j = 0; j < columns; j++) {
                const valBef = pixelArray[i * columns + j];
                let val = valBef
                if (windowCenter && windowWidth) {
                    if (val <= (windowCenter - 0.5 - (windowWidth - 1) / 2))
                        val = 0
                    else if (val > (windowCenter - 0.5 + (windowWidth - 1) / 2))
                        val = 255
                    else
                        val = ~~(((val - (windowCenter - 0.5)) / (windowWidth - 1) + 0.5) * 255)
                } else if (BitsAllocated === 16)
                    val = ((val * 2 ** 8) / 2 ** BitsStored);
                // console.log(`I : ${i} J : ${j} Pixel : ${valBef} Value : ${val}`)
                res.set(rows - i, j, val)
            }
        }
    }
    else{
        for (i = 0; i < rows; i++) {
            for (j = 0; j < columns; j++) {
                const valBef = pixelArray[i * columns + j];
                let val = valBef
                if (windowCenter && windowWidth) {
                    if (val <= (windowCenter - 0.5 - (windowWidth - 1) / 2))
                        val = 0
                    else if (val > (windowCenter - 0.5 + (windowWidth - 1) / 2))
                        val = 255
                    else
                        val = ~~(((val - (windowCenter - 0.5)) / (windowWidth - 1) + 0.5) * 255)
                } else if (BitsAllocated === 16)
                    val = ((val * 2 ** 8) / 2 ** BitsStored);
                // console.log(`I : ${i} J : ${j} Pixel : ${valBef} Value : ${val}`)
                res.set(i, j, val)
            }
        }
    }

    return {
        buffer: res,
        height: rows,
        width: columns,
        ext: 'png',
    }
}