import React from 'react';
import {
  WrappedPanelStudyBrowser,
  PanelMeasurementTable,
  Edition3DPanel,
  PanoramicGenerationPanel
} from './Panels';
import ActionButtons from "./Panels/ActionButtons";

// TODO:
// - No loading UI exists yet
// - cancel promises when component is destroyed
// - show errors in UI for thumbnails if promise fails

function getPanelModule({
  commandsManager,
  extensionManager,
  servicesManager,
}) {
  const wrappedMeasurementPanel = () => {
    return (
      <PanelMeasurementTable
        commandsManager={commandsManager}
        servicesManager={servicesManager}
        extensionManager={extensionManager}
      />
    );
  };

  return [
    {
      name: 'seriesList',
      iconName: 'group-layers',
      iconLabel: 'Studies',
      label: 'Studies',
      component: WrappedPanelStudyBrowser.bind(null, {
        commandsManager,
        extensionManager,
        servicesManager,
      }),
    },
    {
      name: 'measure',
      iconName: 'tab-linear',
      iconLabel: 'Measure',
      label: 'Measurements',
      secondaryLabel: 'Measurements',
      component: wrappedMeasurementPanel,
    },
    {
      name: 'volumeEditionPanel',
      iconName: 'edition-3D',
      iconLabel: '3D Edition',
      label: '3D Edition',
      component: Edition3DPanel.bind(null, {
        commandsManager,
        extensionManager,
        servicesManager
      }),
    },
    {
      name: 'panoramicGenerationPanel',
      iconName: 'panoramic',
      iconLabel: 'Panoramic Generation',
      label: 'Panoramic',
      component: PanoramicGenerationPanel.bind(null, {
        commandsManager,
        extensionManager,
        servicesManager
      }),
    },
  ];
}

export default getPanelModule;
