import vtkPolyData from "@kitware/vtk.js/Common/DataModel/PolyData";
import vtkMath from "@kitware/vtk.js/Common/Core/Math";
const math = require('mathjs');

class Quadric {
    constructor(points) {
        // Quadric matrix in format (A, b, c). A is a 3x3 matrix, b is a Vector3 and c a constant
        // This represents the 10 coefficients of a 4x4 quadric matrix that can be computed using
        // the cartesian coefficients of a plane or with its normal vector and its centroid
        const n = this.getNormal(points);
        const p = this.getCentroid(points);

        // Coefficients of symmetric 3x3 matrix: a11, a12, a13, a22, a23, a33
        this.Axx = n[0] * n[0];
        this.Axy = n[0] * n[1];
        this.Axz = n[0] * n[2];
        this.Ayy = n[1] * n[1];
        this.Ayz = n[1] * n[2];
        this.Azz = n[2] * n[2];

        const vx = this.Axx * p[0] + this.Axy * p[1] + this.Axz * p[2];
        const vy = this.Axy * p[0] + this.Ayy * p[1] + this.Ayz * p[2];
        const vz = this.Axz * p[0] + this.Ayz * p[1] + this.Azz * p[2];
        this.bx = -vx;
        this.by = -vy;
        this.bz = -vz;

        this.c = math.dot(p, [vx, vy, vz]);
    }

    add = (quadric) => {
        this.Axx += quadric.Axx;
        this.Axy += quadric.Axy;
        this.Axz += quadric.Axz;
        this.Ayy += quadric.Ayy;
        this.Ayz += quadric.Ayz;
        this.Azz += quadric.Azz;
        this.bx += quadric.bx;
        this.by += quadric.by;
        this.bz += quadric.bz;
        this.c += quadric.c;

        return this;
    }

    getCentroid = (points) => {
        // Get the center point of a triangle
        const [p0, p1, p2] = points;
        return [
            (p0[0] + p1[0] + p2[0]) / 3,
            (p0[1] + p1[1] + p2[1]) / 3,
            (p0[2] + p1[2] + p2[2]) / 3
        ];
    }

    getNormal = (points) => {
        // Get the normal vector of a triangle
        // This is obtained by normalizing the cross-product of vectors p1-p0 and p2-p0
        const [p0, p1, p2] = points;
        let p10 = new Array(3);
        let p20 = new Array(3);
        let n = new Array(3);

        vtkMath.subtract(p1, p0, p10);
        vtkMath.subtract(p2, p0, p20);
        vtkMath.cross(p10, p20, n);
        vtkMath.normalize(n);

        return n;
    }

    // Computes the error cost over a point p
    // Return p.T@A@p + 2*p.T@b + c
    evaluate = (point) => {
        const x = this.Axx * point[0] + this.Axy * point[1] + this.Axz * point[2];
        const y = this.Axy * point[0] + this.Ayy * point[1] + this.Ayz * point[2];
        const z = this.Axz * point[0] + this.Ayz * point[1] + this.Azz * point[2];
        const error = (point[0] * x + point[1] * y + point[2] * z)
            + 2 * (point[0] * this.bx + point[1] * this.by + point[2] * this.bz) + this.c;

        return error;
    }

    optimalPoint = (v_) => {
        // The computation of optimal point for quadric Q_ corresponds to doing -A.inv()@b
        let a11 = this.Azz * this.Ayy - this.Ayz * this.Ayz;
        let a12 = this.Axz * this.Ayz - this.Azz * this.Axy;
        let a13 = this.Axy * this.Ayz - this.Axz * this.Ayy;
        let a22 = this.Azz * this.Axx - this.Axz * this.Axz;
        let a23 = this.Axy * this.Axz - this.Axx * this.Ayz;
        let a33 = this.Axx * this.Ayy - this.Axy * this.Axy;
        let det = (this.Axx * a11) + (this.Axy * a12) + (this.Axz * a13);

        // Check if the matrix is not singular (non invertible) or almost not singular (< epsilon)
        if (math.abs(det) > 0.000000001) {
            det = 1.0 / det;
            a11 *= det;
            a12 *= det;
            a13 *= det;
            a22 *= det;
            a23 *= det;
            a33 *= det;
            // Return value is parameter v_
            v_[0] = -(a11 * this.bx + a12 * this.by + a13 * this.bz);
            v_[1] = -(a12 * this.bx + a22 * this.by + a23 * this.bz);
            v_[2] = -(a13 * this.bx + a23 * this.by + a33 * this.bz);
            return true;
        } else {
            // Cannot compute optimal point
            // Needs to take the best point between the middle point and the ends
            return false;
        }
    }
}

function VertexPairCost(v1Id, v2Id, cost, v_) {
    this.v1Id = v1Id;
    this.v2Id = v2Id;
    this.cost = cost;
    this.v_ = v_;
}

class QuadricDecimator {
    constructor(polyData, targetReduction = 0.5) {
        console.log('QuadricDecimation: Init QuadricDecimator');
        this.polyData = this.copy(polyData);
        this.targetReduction = targetReduction;

        this.points = this.polyData.getPoints();
        if (!this.polyData.getLinks()) {
            console.log('QuadricDecimation: Build Links');
            this.polyData.buildLinks();
        }
        console.log('QuadricDecimation: Init Lines');
        // Store lines in a different structure prevent function overload
        this.lines = this.initLines();
        //this.neighbors = this.initNeighbors();

        console.log('QuadricDecimation: Init QMatrices');
        this.qMatrices = this.initQuadricMatrices();
        console.log('QuadricDecimation: Init PairCosts');
        this.pairCosts = this.initPairCosts();
        this.currentNumberOfCells = this.polyData.getNumberOfCells();
        this.currentNumberOfPoints = this.polyData.getNumberOfPoints();
    }

    copy = (polyData) => {
        const copy = vtkPolyData.newInstance();
        copy.getPoints().setData(polyData.getPoints().getTuples(), 3);
        copy.getPolys().setData(polyData.getPolys().getTuples());
        copy.getPolys().setNumberOfComponents(4);

        return copy;
    }

    setTargetReduction = (target_reduction) => {
        this.target_reduction = target_reduction;
    }

    initLines = () => {
        const lineSet = new Set();
        for (let cellId = 0; cellId < this.polyData.getNumberOfCells(); cellId++) {
            const pointIds = this.polyData.getCellPoints(cellId).cellPointIds;
            const [pointId1, pointId2, pointId3] = pointIds.sort((a, b) => a - b);
            lineSet.add(`${pointId1}-${pointId2}`);
            lineSet.add(`${pointId1}-${pointId3}`);
            lineSet.add(`${pointId2}-${pointId3}`);
        }

        const lines = Array.from(lineSet).sort((a, b) => {
            const [id1A, id2A] = a.split('-').map(Number);
            const [id1B, id2B] = b.split('-').map(Number);
            return (id1A - id1B) || (id2A - id2B);
        });

        return lines;
    }

    initNeighbors = () => {
        let neighbors = Array.from( { length: this.polyData.getNumberOfPoints() }, () => new Set());
        for (const line of this.lines) {
            const [pointId1, pointId2] = line.split('-').map(Number);
            neighbors[pointId1].add(pointId2);
            neighbors[pointId2].add(pointId1);
        }

        return neighbors;
    }

    initQuadricMatrices = () => {
        // QuadricMatrices contains a 4-4 matrix of coefficients at each point
        let quadricMatrices = Array.from({ length: this.polyData.getNumberOfPoints() });

        // Loop over all cells to fill the Q matrices for all initial vertices
        for (let cellId = 0; cellId < this.polyData.getNumberOfCells(); cellId++) {
            const pointIds = Array.from(this.polyData.getCellPoints(cellId).cellPointIds);
            const points = pointIds.map(pointId => this.points.getPoint(pointId));

            // Old method storing values in a 16 components
            // p = [a b c d] represents the plane defined by the equation ax + by + cz + d = 0
            // Kp = p@p^t = [[a,ab,ac,ad],[ab,b,bc,bd],[ac,bc,c,cd],[ad,bd,cd,d]]
            /*const k = math.multiply(math.transpose([p]), [p]);
            for (const pointId of pointIds) {
                quadricMatrices[pointId] = math.add(quadricMatrices[pointId], k);
            }*/

            // New method storing same values in 10 components
            const q = new Quadric(points);
            for (const pointId of pointIds) {
                if (!quadricMatrices[pointId]) {
                    quadricMatrices[pointId] = q;
                } else {
                    quadricMatrices[pointId].add(q);
                }
            }
        }

        return quadricMatrices;
    }

    _computeQ_V_Cost = (pointId1, pointId2) => {
        // Compute Q_
        const q1 = this.qMatrices[pointId1];
        const q2 = this.qMatrices[pointId2];
        const q_ = q1.add(q2);


        // Compute V_
        let v_ = Array.from( {length: 3} );
        let cost;
        // Compute optimal point from Quadric Q_
        if (q_.optimalPoint(v_)) {
            cost = q_.evaluate(v_);
        } else {
            // Need to compare middle point with ends
            const p1 = this.points.getPoint(pointId1);
            const p2 = this.points.getPoint(pointId2);
            const middlePoint = [(p1[0] + p2[0]) / 2, (p1[1] + p2[1]) / 2, (p1[2] + p2[2]) / 2];
            const errorP1 = q_.evaluate(p1);
            const errorP2 = q_.evaluate(p2);
            const errorMp = q_.evaluate(middlePoint);
            if (errorP1 < errorP2 && errorP1 < errorMp) {
                v_ = p1;
                cost = errorP1;
            } else if (errorP2 < errorMp) {
                v_ = p2;
                cost = errorP2;
            } else {
                v_ = middlePoint;
                cost = errorMp;
            }
        }

        return [q_, v_, cost];
    }

    initPairCosts = () => {
        let pairCosts = Array.from({ length: this.lines.length }).fill(false);;
        const nbPairs = this.lines.length;
        console.log(`QuadricDecimation - vertex pairs: ${nbPairs}`);
        this.lines.forEach((line, lineId) => {
            const [pointId1, pointId2] = line.split('-').map(Number);
            const [q_, v_, cost] = this._computeQ_V_Cost(pointId1, pointId2);
            const pairCost = new VertexPairCost(Number(pointId1), Number(pointId2), cost, v_);
            pairCosts[lineId] = pairCost;
        });

        return pairCosts;
    }

    buildMinHeap = () => {
        const heapSize = this.pairCosts.length;

        // Start from the last non-leaf node and heapify each node
        for (let i = ~~(heapSize / 2) - 1; i >= 0; i--) {
          this.heapify(i, heapSize);
        }
    }

    heapify = (index, heapSize) => {
        const costs = this.pairCosts;
        const leftChildIndex = 2 * index + 1;
        const rightChildIndex = 2 * index + 2;
        let smallestIndex = index;

        // Compare with left child
        if (leftChildIndex < heapSize && costs[leftChildIndex].cost < costs[smallestIndex].cost) {
          smallestIndex = leftChildIndex;
        }

        // Compare with right child
        if (rightChildIndex < heapSize && costs[rightChildIndex].cost < costs[smallestIndex].cost) {
          smallestIndex = rightChildIndex;
        }

        // If the smallest element is not the current root, or if it has the same cost,
        // v1Id, and v2Id, swap them and recursively heapify
        if (smallestIndex !== index) {
            [costs[index], costs[smallestIndex]] = [costs[smallestIndex], costs[index]];
            this.heapify(smallestIndex, heapSize);
        }
    }

    extractMin = () => {
        if (this.pairCosts.length === 0) {
          return null; // Heap is empty
        }

        const minElement = this.pairCosts[0];

        // Replace the root with the last element
        this.pairCosts[0] = this.pairCosts.pop();

        // Restore the heap property starting from the root
        this.heapify(0, this.pairCosts.length);

        return minElement;
    }

    _updateCells = (minPairCost) => {
        const minV1Id = minPairCost.v1Id;
        const minV2Id = minPairCost.v2Id;

        // Remove cells with edge minV1-minV2
        const neighborCells = this.polyData.getCellEdgeNeighbors(-1, minV1Id, minV2Id);
        for (const cellId of neighborCells) {
            // Remove reference from points to cell
            const pointIds = this.polyData.getCellPoints(cellId).cellPointIds;
            for (const pointId of pointIds) {
                this.polyData.getLinks().removeCellReference(cellId, pointId);
            }
            this.polyData.getCells().deleteCell(cellId);
            this.currentNumberOfCells--;
            // Store IDs of deleted cells for reallocation
            this.deletedCells[cellId] = true;
        }

        // Replace occurrences of minV2 with minV1
        for (const cellId of this.polyData.getPointCells(minV2Id)) {
            if (!this.deletedCells[cellId]) {
                let pointIds = this.polyData.getCellPoints(cellId).cellPointIds;
                pointIds = pointIds.map((pointId) => (pointId === minV2Id) ? minV1Id : pointId);
                this.polyData.getPolys().setTuple(cellId, [3, ...pointIds]);

                // Add reference to cell for minV1
                if (!neighborCells.includes(cellId)) {
                    this.polyData.getLinks().insertNextCellReference(minV1Id, cellId);
                }
            }
        }
    }

    _updatePoints = (minPairCost) => {
        const minV1Id = minPairCost.v1Id;
        const minV2Id = minPairCost.v2Id;
        const minV_ = minPairCost.v_;

        // Update coordinates of v1 with v_, delete v2
        this.points.setPoint(minV1Id, minV_[0], minV_[1], minV_[2]);

        this.deletedPoints[minV2Id] = true;
        this.polyData.getLinks().deletePoint(minV2Id);
        --this.currentNumberOfPoints;
    }

    _updatePairCosts = (minPairCost) => {
        const minV1Id = minPairCost.v1Id;
        const minV2Id = minPairCost.v2Id;

        // Update edges optimal contraction and cost
        for (let pairId = 0; pairId < this.pairCosts.length; pairId++) {
            const curV1Id = this.pairCosts[pairId].v1Id;
            const curV2Id = this.pairCosts[pairId].v2Id;
            // Update v_ and cost as minV1 got updated
            if (curV1Id === minV1Id || curV2Id === minV1Id) {
                const [newQ_, newV_, newCost] = this._computeQ_V_Cost(curV1Id, curV2Id);
                this.pairCosts[pairId] = new VertexPairCost(curV1Id, curV2Id, newCost, newV_);
            }
            // Replace minV2 with minV1 as minV2 got removed, then update v_ and cost
            else if (curV1Id === minV2Id || curV2Id === minV2Id) {
                const otherVId = (curV1Id !== minV2Id)? curV1Id : curV2Id;
                const [newV1Id, newV2Id] = [minV1Id, otherVId].sort((a, b) => a - b);
                const [newQ_, newV_, newCost] = this._computeQ_V_Cost(newV1Id, newV2Id);
                if (this.pairCosts.some(pairCost => pairCost.v1Id === newV1Id && pairCost.v2Id === newV2Id)) {
                    // Remove element
                    const lastIndex = this.pairCosts.length - 1;
                    [this.pairCosts[pairId], this.pairCosts[lastIndex]] = [this.pairCosts[lastIndex], this.pairCosts[pairId]];
                    this.pairCosts.pop();
                    pairId--;
                } else {
                    this.pairCosts[pairId] = new VertexPairCost(newV1Id, newV2Id, newCost, newV_);
                }
            }
        }
    }

    _removeDegenerate = (pointId) => {
        // Remove degenerate cells
        // TODO Some code duplicate
        if (this.deletedPoints[pointId]) {
            return;
        }

        for (const cellId of this.polyData.getPointCells(pointId)) {
            const degenerateCellPointIds = this.polyData.getCellPoints(cellId).cellPointIds;
            for (const degenerateCellPointId of degenerateCellPointIds) {
                this.polyData.getLinks().removeCellReference(cellId, degenerateCellPointId);
            }
            this.polyData.getCells().deleteCell(cellId);
            this.currentNumberOfCells--;
            this.deletedCells[cellId] = true;
        }

        // Delete degenerate point
        this.deletedPoints[pointId] = true;
        this.polyData.getLinks().deletePoint(pointId);
        --this.currentNumberOfPoints;

        // Delete degenerate pairs
        for (let pairId = 0; pairId < this.pairCosts.length; pairId++) {
            let pairCost = this.pairCosts[pairId];
            if (pairCost.v1Id === pointId || pairCost.v2Id === pointId) {
                const lastIndex = this.pairCosts.length - 1;
                [this.pairCosts[pairId], this.pairCosts[lastIndex]] = [this.pairCosts[lastIndex], this.pairCosts[pairId]];
                this.pairCosts.pop();
                pairId--;
            }
        }

        // Remove neighbors
        const pointNeighbors = Array.from(this.neighbors[pointId]);
        for (const neighborPointId of pointNeighbors) {
            this.neighbors[neighborPointId].delete(pointId);
        }
        this.neighbors[pointId].clear();
        /*for (const neighborPointId of pointNeighbors) {
            if (this.neighbors[neighborPointId].size < 3 || this.polyData.getPointCells(neighborPointId).length < 3) {
                this._removeDegenerate(neighborPointId);
            }
        }*/
    }

    _updateNeighbors = (minPairCost) => {
        const minV1Id = minPairCost.v1Id;
        const minV2Id = minPairCost.v2Id;

        const minV2Neighbors = Array.from(this.neighbors[minV2Id]);
        // Remove references from points to minV2
        for (const pointId of minV2Neighbors) {
            this.neighbors[pointId].delete(minV2Id);
            if (pointId !== minV1Id) {
                this.neighbors[pointId].add(minV1Id);
                this.neighbors[minV1Id].add(pointId);
            }
        }
        this.neighbors[minV2Id].clear();
        /*for (const pointId of minV2Neighbors) {
            if (this.neighbors[pointId].size < 3 || this.polyData.getPointCells(pointId).length < 3) { // Degenerate Point
                this._removeDegenerate(pointId);
            }
        }*/
    }

    decimate = () => {
        if (this.targetReduction <= 0. || this.targetReduction >= 1.) {
            return this.polyData;
        }

        const originalNumberOfCells = this.polyData.getNumberOfCells();
        const originalNumberOfPoints = this.polyData.getNumberOfPoints();
        const targetNumberOfCells = ~~(this.targetReduction * originalNumberOfCells);
        this.deletedCells = Array.from({ length: originalNumberOfCells }).fill(false);
        this.deletedPoints = Array.from({ length: originalNumberOfPoints }).fill(false);

        this.buildMinHeap();
        while (this.currentNumberOfCells > targetNumberOfCells) {
            if ((originalNumberOfCells - this.currentNumberOfCells) % 100 === 0) {
                console.log(`QuadricDecimation - cells destroyed: ${originalNumberOfCells - this.currentNumberOfCells}`
                            + ` / ${originalNumberOfCells - targetNumberOfCells}`);
            }
            const minPairCost = this.extractMin();
            this._updateCells(minPairCost);
            this._updatePoints(minPairCost);
            this._updatePairCosts(minPairCost);
            //this._updateNeighbors(minPairCost);
            this.buildMinHeap();
        }

        /*for (let pointId = 0; pointId < originalNumberOfPoints; pointId++) {
            if (!this.deletedPoints[pointId] && this.polyData.getLinks().getLink(0).ncells < 3) {
                this._removeDegenerate(pointId);
            }
        }*/

        // TODO Reallocate points and polys
        let pBuffer = [];
        let tBuffer = [];
        let pointMap = Array.from({ length: originalNumberOfPoints }).fill(-1);
        /*let b = false;
        while (!b) {
            b = true;
            for (let pointId = 0; pointId < originalNumberOfPoints; pointId++) {
                if (!this.deletedPoints[pointId] && (this.neighbors[pointId].size < 3 || this.polyData.getPointCells(pointId).length < 3)) { // Degenerate Point
                    b = false;
                    console.log(originalNumberOfPoints)
                    this._removeDegenerate(pointId);
                }
            }
        }*/

        let i = 0;
        for (let pointId = 0; pointId < originalNumberOfPoints; pointId++) {
            if (!this.deletedPoints[pointId]) {
                pBuffer.push(...this.points.getPoint(pointId));
                pointMap[pointId] = i;
                i++;
            }
        }
        for (let cellId = 0; cellId < originalNumberOfCells; cellId++) {
            if (!this.deletedCells[cellId]) {
                tBuffer.push(3);
                let pointIds = this.polyData.getCellPoints(cellId).cellPointIds;
                pointIds = pointIds.map(pointId => pointMap[pointId]);
                tBuffer.push(...Array.from(pointIds));
            }
        }

        const newPolyData = vtkPolyData.newInstance();
        newPolyData.getPoints().setData(new Float32Array(pBuffer), 3)
        newPolyData.getPolys().setData(new Uint32Array(tBuffer));
        newPolyData.getPolys().setNumberOfComponents(4);

        return newPolyData;
    }
}

export default QuadricDecimator;
